
    import { defineComponent, ref, watch } from "vue";

    export default defineComponent({
        name: "RowBytton",
        props: {
            label: String,
            title: {
                type: String,
                default: null
            },
        },
        setup(props, { emit }) {
            const handleClick = (evt) => {
                emit("clicked", evt);
            };

            return {
                handleClick,
            };
        },
    });
